.footer-container {
  display: flex;
  justify-content: center;
  align-items: center;
  margin: auto;
  min-width: 80%;
  width: 87%;
}

.footer-row {
  width: 100%;
  margin-top: 3%;
}

.button-icon {
  border: none;
  background: none;
}

.logo {
  height: 20px;
  width: 20px;
  margin-right: 12px;
}

.logo-col {
  margin-bottom: 1rem;
  display: flex;
  justify-content: flex-end;
  align-items: flex-start;
}

.text-col {
  display: flex;
  justify-content: flex-start;
  align-items: flex-start;
  flex-direction: row;
}

.name {
  color: rgb(234, 0, 255);
}

.footer-p {
  font-size: 15px;
}
